}
.star-ratings h1,
.star-ratings p {
  text-align: center;
}
.star-ratings .stars {
  width: 120px;
  text-align: center;
  margin: auto;
  padding: 0 95px;
}
.star-ratings .stars .title {
  font-size: 14px;
  color: #cccccc;
  line-height: 3;
}
.star-ratings .stars select {
  width: 120px;
  font-size: 16px;
}
